import {AUTO_PRO} from './covid';
import { useState } from 'react';
import heart from '../assets/heart.png';
import { useDispatch } from 'react-redux';
import { next } from '../redux_logic/action_creators';

export const Comments = ({comments, setComments, submit_function}) => {
  let arr1 = ['рЃЎрЃЋрЃўрЃарЃљрЃерЃў рЃЮрЃарЃ»рЃћрЃа', 'рЃЎрЃЋрЃўрЃарЃљрЃерЃў рЃћрЃарЃЌрЃ«рЃћрЃџ', 'рЃЮрЃа рЃЎрЃЋрЃўрЃарЃљрЃерЃў рЃћрЃарЃЌрЃ«рЃћрЃџ', 'рЃЌрЃЋрЃћрЃерЃў рЃћрЃарЃЌрЃ«рЃћрЃџ'];
  let arr2 = ['0', '1', '2', '3', '4', '5'];
  let [error1, setError1] = useState('');
  let [error2, setError2] = useState('');

  const dispatch = useDispatch();
  const handler_func = () => {
    if(comments.рЃЮрЃюрЃџрЃљрЃўрЃю_рЃерЃћрЃ«рЃЋрЃћрЃЊрЃарЃћрЃЉрЃў === ''){
      setError1('рЃљрЃЏ рЃЋрЃћрЃџрЃўрЃА рЃерЃћрЃЋрЃАрЃћрЃЉрЃљ рЃАрЃљрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃБрЃџрЃЮрЃљ')
    }else if(comments.рЃЮрЃцрЃўрЃАрЃўрЃЊрЃљрЃю_рЃЏрЃБрЃерЃљрЃЮрЃЉрЃљ === ''){
      setError2('рЃљрЃЏ рЃЋрЃћрЃџрЃўрЃА рЃерЃћрЃЋрЃАрЃћрЃЉрЃљ рЃАрЃљрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃБрЃџрЃЮрЃљ')
    }else {
      submit_function();
      dispatch(next());
    }
  }
  return (
    <>
    <div className='radios'>
      <p className='intro'>
       рЃарЃћрЃЊрЃЉрЃћрЃарЃўрЃА рЃЏрЃЌрЃљрЃЋрЃљрЃарЃў рЃдрЃўрЃарЃћрЃЉрЃБрЃџрЃћрЃЉрЃљ рЃЕрЃЋрЃћрЃюрЃў рЃњрЃБрЃюрЃЊрЃўрЃА рЃЌрЃўрЃЌрЃЮрЃћрЃБрЃџрЃў рЃгрЃћрЃЋрЃарЃўрЃљ. рЃњрЃљрЃарЃћрЃЏрЃЮ, рЃарЃЮрЃЏрЃћрЃџрЃАрЃљрЃф рЃЕрЃЋрЃћрЃюрЃў
      рЃЌрЃљрЃюрЃљрЃЏрЃерЃарЃЮрЃЏрЃџрЃћрЃЉрЃў рЃЦрЃЏрЃюрЃўрЃЌ, рЃЉрЃћрЃЋрЃарЃўрЃАрЃЌрЃЋрЃўрЃА рЃљрЃарЃўрЃА рЃЊрЃљ рЃДрЃЮрЃцрЃўрЃџрЃљ рЃгрЃџрЃћрЃЉрЃўрЃА рЃњрЃљрЃюрЃЏрЃљрЃЋрЃџрЃЮрЃЉрЃљрЃерЃў рЃЏрЃўрЃќрЃюрЃћрЃЉрЃўрЃАрЃЌрЃЋрЃўрЃА рЃћрЃарЃЌрЃљрЃЊ рЃЉрЃарЃФрЃЮрЃџрЃўрЃА рЃЏрЃўрЃќрЃћрЃќрЃў, рЃЉрЃћрЃЋрЃарЃўрЃАрЃЌрЃЋрЃўрЃА рЃЎрЃў Рђћ рЃЕрЃЋрЃћрЃюрЃЌрЃљрЃю рЃњрЃљрЃЊрЃЏрЃЮрЃАрЃЋрЃџрЃўрЃА.<br></br><br></br>
      рЃърЃљрЃюрЃЊрЃћрЃЏрЃўрЃўрЃА рЃърЃћрЃарЃўрЃЮрЃЊрЃерЃў рЃћрЃарЃЌрЃЏрЃљрЃюрЃћрЃЌрЃАрЃљрЃф рЃўрЃерЃЋрЃўрЃљрЃЌрЃљрЃЊ рЃЋрЃюрЃљрЃ«рЃБрЃџрЃЮрЃЉрЃЌ рЃърЃўрЃарЃўрЃАрЃърЃўрЃа рЃЊрЃљ рЃДрЃЮрЃЋрЃћрЃџрЃЊрЃдрЃўрЃБрЃарЃў рЃЎрЃЮрЃЏрЃБрЃюрЃўрЃЎрЃљрЃфрЃўрЃљрЃф рЃњрЃљрЃўрЃерЃЋрЃўрЃљрЃЌрЃЊрЃљ.
      </p>
      {
        AUTO_PRO(arr1, 'рЃЮрЃюрЃџрЃљрЃўрЃю_рЃерЃћрЃ«рЃЋрЃћрЃЊрЃарЃћрЃЉрЃў', 'рЃарЃљ рЃАрЃўрЃ«рЃерЃўрЃарЃўрЃЌ рЃерЃћрЃўрЃФрЃџрЃћрЃЉрЃљ рЃњрЃЋрЃЦрЃЮрЃюрЃЊрЃћрЃА рЃАрЃљрЃћрЃарЃЌрЃЮ рЃљрЃарЃљрЃцрЃЮрЃарЃЏрЃљрЃџрЃБрЃарЃў рЃЮрЃюрЃџрЃљрЃўрЃю рЃерЃћрЃ«рЃЋрЃћрЃЊрЃарЃћрЃЉрЃў, рЃАрЃљрЃЊрЃљрЃф рЃДрЃЋрЃћрЃџрЃљ рЃАрЃБрЃарЃЋрЃўрЃџрЃўрЃАрЃљрЃЏрЃћрЃЉрЃа рЃЕрЃљрЃћрЃарЃЌрЃЋрЃћрЃЉрЃљ?*', 'рЃЮрЃюрЃџрЃљрЃўрЃю_рЃерЃћрЃ«рЃЋрЃћрЃЊрЃарЃћрЃЉрЃў', comments, setComments)
      }
      <span className='error-span'>{error1}</span>
      {
        AUTO_PRO(arr2, 'рЃЮрЃцрЃўрЃАрЃўрЃЊрЃљрЃю_рЃЏрЃБрЃерЃљрЃЮрЃЉрЃљ', 'рЃЎрЃЋрЃўрЃарЃљрЃерЃў рЃарЃљрЃЏрЃЊрЃћрЃюрЃў рЃЊрЃдрЃћ рЃўрЃАрЃБрЃарЃЋрЃћрЃЉрЃЊрЃў рЃЮрЃцрЃўрЃАрЃўрЃЊрЃљрЃю рЃЏрЃБрЃерЃљрЃЮрЃЉрЃљрЃА?*', 'рЃЮрЃцрЃўрЃАрЃўрЃЊрЃљрЃю_рЃЏрЃБрЃерЃљрЃЮрЃЉрЃљ', comments, setComments)
      }
      <span className='error-span'>{error2}</span>
      <p className='question'>рЃарЃљрЃА рЃцрЃўрЃЦрЃарЃЮрЃЉ рЃцрЃўрЃќрЃўрЃЎрЃБрЃа рЃерЃћрЃЎрЃарЃћрЃЉрЃћрЃЉрЃќрЃћ?</p>
      <textarea value={comments.рЃцрЃўрЃќрЃўрЃЎрЃБрЃарЃў_рЃерЃћрЃЎрЃарЃћрЃЉрЃћрЃЉрЃў} onChange={(e) => setComments({...comments, рЃцрЃўрЃќрЃўрЃЎрЃБрЃарЃў_рЃерЃћрЃЎрЃарЃћрЃЉрЃћрЃЉрЃў: e.target.value})}></textarea>
      <p className='question'>рЃарЃљрЃА рЃцрЃўрЃЦрЃарЃЮрЃЉ рЃљрЃарЃАрЃћрЃЉрЃБрЃџ рЃњрЃљрЃарЃћрЃЏрЃЮрЃќрЃћ: <br></br>
        рЃарЃљ рЃЏрЃЮрЃњрЃгрЃЮрЃюрЃА, рЃарЃљрЃА рЃЊрЃљрЃљрЃЏрЃљрЃбрЃћрЃЉрЃЊрЃў, рЃарЃљрЃА рЃерЃћрЃфрЃЋрЃџрЃўрЃЊрЃў?
      </p>
      <textarea value={comments.рЃњрЃљрЃарЃћрЃЏрЃЮ} onChange={(e) => setComments({...comments, рЃњрЃљрЃарЃћрЃЏрЃЮ: e.target.value})}></textarea>
      <button className='submit' type='button' onClick={() => handler_func()}>рЃЊрЃљрЃАрЃарЃБрЃџрЃћрЃЉрЃљ</button>
    </div>
    <img className='heart' src={heart} alt='heart shape'></img>
    </>
  )
}
