import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { enable, disable } from '../redux_logic/action_creators';

//Function for generating radio inputs easier. We can use this function within
//other modules too

export const AUTO_PRO = (data, name, p_text, state, stateObj, setStateObj) => {
   return (
   <>
   <p className='question'>{p_text}</p>
   {data.map((i) => {
     return (
      <label key={i}>
      <input className='radio-input' name={name} type="radio" id={i} onChange={
         (e) => setStateObj({...stateObj, [state]: e.target.id})
      } checked={stateObj[state] === i}/>
      {i}
      <br></br>
      </label>
     )
   })}
   </>
   )
}
export const Covid = ({covidState,  setCovidState}) => {
   const dispatch = useDispatch();
   const arr1 = ["рЃЎрЃў", "рЃљрЃарЃљ", "рЃљрЃ«рЃџрЃљ рЃЏрЃљрЃЦрЃЋрЃА"];
   const arr2 = ["рЃЎрЃў", "рЃљрЃарЃљ"];
   useEffect(() => {
      let [a,b,c,d,e] = Object.values(covidState);
      if(a==="рЃљрЃарЃљ" || a==="рЃљрЃ«рЃџрЃљ рЃЏрЃљрЃЦрЃЋрЃА" || (a==="рЃЎрЃў" && b==="рЃЎрЃў" && c && d) || (a==="рЃЎрЃў" && b==="рЃљрЃарЃљ" && e)){
         dispatch(enable());
      }else{
         dispatch(disable());
      }
   }, [covidState, dispatch]);

//Let's handle all the nasty toggling between options accurately :D

   const handleChange = (e) => {
      if(e.target.id === 'test_date' || e.target.id === 'test_result'){
         setCovidState({...covidState, [e.target.id]: e.target.value, 'covid_date': ''});
      }
      if(e.target.id === 'covid_date'){
         setCovidState({...covidState, [e.target.id]: e.target.value, 'test_date': '', 'test_result': ''});
      }
   }

   return (
     <>
     <div className="radios">
      { AUTO_PRO(arr1, "рЃњрЃљрЃЊрЃљрЃбрЃљрЃюрЃўрЃџрЃў", 'рЃњрЃљрЃЦрЃЋрЃА рЃњрЃљрЃЊрЃљрЃбрЃљрЃюрЃўрЃџрЃў Covid-19?*', 'рЃњрЃљрЃЊрЃљрЃбрЃљрЃюрЃўрЃџрЃў', covidState, setCovidState) }
      {covidState.рЃњрЃљрЃЊрЃљрЃбрЃљрЃюрЃўрЃџрЃў === 'рЃЎрЃў' && AUTO_PRO(arr2, "рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА рЃбрЃћрЃАрЃбрЃў", 'рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА рЃбрЃћрЃАрЃбрЃў рЃњрЃљрЃЎрЃћрЃЌрЃћрЃЉрЃБрЃџрЃў рЃњрЃљрЃЦрЃЋрЃА?*', 'рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА_рЃбрЃћрЃАрЃбрЃў', covidState, setCovidState)}
       {covidState.рЃњрЃљрЃЊрЃљрЃбрЃљрЃюрЃўрЃџрЃў === 'рЃЎрЃў' && covidState.рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА_рЃбрЃћрЃАрЃбрЃў === 'рЃЎрЃў' && (
           <>
               <p className='question'>рЃЌрЃБ рЃњрЃљрЃ«рЃАрЃЮрЃЋрЃА, рЃњрЃЌрЃ«рЃЮрЃЋ рЃЏрЃўрЃБрЃЌрЃўрЃЌрЃћ рЃбрЃћрЃАрЃбрЃўрЃА рЃЏрЃўрЃљрЃ«рЃџрЃЮрЃћрЃЉрЃўрЃЌрЃў рЃарЃўрЃфрЃ«рЃЋрЃў рЃЊрЃљ рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА рЃарЃљрЃЮрЃЊрЃћрЃюрЃЮрЃЉрЃљ*</p>
               <input value={covidState.test_date} className='custom-input' id='test_date' type='date' onChange={handleChange}></input><br></br>
               <input value={covidState.test_result} className='custom-input' id='test_result' type='number' onChange={handleChange}></input>
           </>
       )
      }
      {
         covidState.рЃњрЃљрЃЊрЃљрЃбрЃљрЃюрЃўрЃџрЃў === 'рЃЎрЃў' && covidState.рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА_рЃбрЃћрЃАрЃбрЃў === 'рЃљрЃарЃљ' && (
           <>
               <p className='question'>рЃЏрЃўрЃБрЃЌрЃўрЃЌрЃћ рЃЏрЃўрЃљрЃ«рЃџрЃЮрЃћрЃЉрЃўрЃЌрЃў рЃърЃћрЃарЃўрЃЮрЃЊрЃў (рЃЊрЃдрЃћ/рЃЌрЃЋрЃћ/рЃгрЃћрЃџрЃў) рЃарЃЮрЃЊрЃўрЃА рЃњрЃЦрЃЮрЃюрЃЊрЃљ Covid-19*</p>
               <input value={covidState.covid_date} className='custom-input' id='covid_date' type='date' onChange={handleChange}></input><br></br>
           </>
       )
      }
     </div>
     <div className='fever'></div>
     </>
   )
}
