import { useEffect, useState, useMemo } from 'react';
import { enable, disable } from '../redux_logic/action_creators';
import { useDispatch } from 'react-redux';

// COMPONENT FOR IDENTIFICATION
export const Target = ({state, setState}) => {
  const dispatch = useDispatch();
  const regex = useMemo(() => /^[a-zA-ZрЃљ-рЃ░]{3,255}$/, []);
  const regex_email = useMemo(() => /^[\w]+[\w-.]+@redberry.ge$/, []);
//We can listen to the state using effect hook as well as check for validation
  useEffect(() => {
    if(regex.test(state.рЃАрЃљрЃ«рЃћрЃџрЃў) && regex.test(state.рЃњрЃЋрЃљрЃарЃў) && regex_email.test(state.рЃЏрЃћрЃўрЃџрЃў)){
      dispatch(enable());
    }else{
      dispatch(disable());
    }
  }, [state, dispatch, regex_email, regex]);

//INPUT VALIDATION SPECIFICS
   let defaultError = {рЃАрЃљрЃ«рЃћрЃџрЃў: '', рЃњрЃЋрЃљрЃарЃў: '', рЃЏрЃћрЃўрЃџрЃў: ''}
   const [error, setError] = useState(defaultError);

// We can combine all handlers into a single function as follows:
  const handleChange = (e) => {
    let val = e.target.value;
    let id = e.target.id;
    setState({...state, [id]: val});
    if(id !== 'рЃЏрЃћрЃўрЃџрЃў' && !regex.test(val)){
// HERE: as '' < 3 returns true the right order works out everything:
      val.length < 3 && setError({...error, [id]: `${id}рЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃЊрЃњрЃћрЃЉрЃЮрЃЊрЃћрЃА рЃЏрЃўрЃюрЃўрЃЏрЃБрЃЏ 3 рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃњрЃљрЃю`});
      val === '' && setError({...error, [id]: `${id}рЃА рЃЋрЃћрЃџрЃўрЃА рЃерЃћрЃЋрЃАрЃћрЃЉрЃљ рЃАрЃљрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃБрЃџрЃЮрЃљ`});
      val.length > 255 && setError({...error, [id]: `${id}рЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃЊрЃњрЃћрЃЉрЃЮрЃЊрЃћрЃА рЃЏрЃљрЃЦрЃАрЃўрЃЏрЃБрЃЏ 255 рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃњрЃљрЃю`});
      val.length > 2 && setError({...error, [id]: `${id}рЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃўрЃфрЃљрЃЋрЃЊрЃћрЃА рЃЏрЃ«рЃЮрЃџрЃЮрЃЊ рЃљрЃюрЃЉрЃљрЃюрЃўрЃА рЃљрЃАрЃЮрЃћрЃЉрЃА`});
    }else if(id === 'рЃЏрЃћрЃўрЃџрЃў' && !regex_email.test(val)){
      setError({...error, [id]: 'рЃњрЃЌрЃ«рЃЮрЃЋрЃЌ рЃЊрЃљрЃарЃћрЃњрЃўрЃАрЃбрЃарЃўрЃарЃЊрЃћрЃЌ Redberry-рЃА рЃЏрЃћрЃўрЃџрЃўрЃЌ (youremail@redberry.ge)'});
    }else{
      setError({...error, [id]: ''});
    }
  }
  return (
    <div className='target'>
      <label className="target-label required" htmlFor="рЃАрЃљрЃ«рЃћрЃџрЃў">рЃАрЃљрЃ«рЃћрЃџрЃў</label>
      <input placeholder='рЃАрЃљрЃ«рЃћрЃџрЃў' className='text-input' id="рЃАрЃљрЃ«рЃћрЃџрЃў" key="рЃАрЃљрЃ«рЃћрЃџрЃў" type="text" value={state.рЃАрЃљрЃ«рЃћрЃџрЃў || ""} onChange={handleChange}></input>
      <span className='error-span'>{error.рЃАрЃљрЃ«рЃћрЃџрЃў}</span>
      <label className="target-label required" htmlFor="рЃњрЃЋрЃљрЃарЃў">рЃњрЃЋрЃљрЃарЃў</label>
      <input placeholder='рЃњрЃЋрЃљрЃарЃў' className='text-input' id="рЃњрЃЋрЃљрЃарЃў" key="рЃњрЃЋрЃљрЃарЃў" type="text" value={state.рЃњрЃЋрЃљрЃарЃў} onChange={handleChange}></input>
      <span className='error-span'>{error.рЃњрЃЋрЃљрЃарЃў}</span>
      <label className="target-label required" htmlFor="рЃЏрЃћрЃўрЃџрЃў">рЃЏрЃћрЃўрЃџрЃў</label>
      <input placeholder='mail@redberry.ge' className='text-input' id="рЃЏрЃћрЃўрЃџрЃў" key="рЃЏрЃћрЃўрЃџрЃў" type="text" value={state.рЃЏрЃћрЃўрЃџрЃў} onChange={handleChange}></input>
      <span className='error-span'>{error.рЃЏрЃћрЃўрЃџрЃў}</span>
      <div className='marginal_note'>
        <hr></hr>
        <span>*-рЃўрЃЌ рЃЏрЃЮрЃюрЃўрЃерЃюрЃБрЃџрЃў рЃЋрЃћрЃџрЃћрЃЉрЃўрЃА рЃерЃћрЃЋрЃАрЃћрЃЉрЃљ рЃАрЃљрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃБрЃџрЃЮрЃљ</span>
      </div>
      <div className='scan'></div>
    </div>
  )
}
